import { autoinject } from "aurelia-framework";
import { Router } from "aurelia-router";
import { DialogService } from "aurelia-dialog";
import * as $ from "jquery";
import "semantic-ui-calendar";
import * as moment from "moment";
import { SubJobs } from "./sub-jobs";
import { Prompt } from "../controls/prompt";
import { JobService } from "../../services/data/job-service";
import { CustomerService } from "../../services/data/customer-service";
import { SubJobOptionService } from "../../services/data/sub-job-service";
import { Notifications } from "../../services/notifications";
import { Job, JobDocument } from "../../models/job";
import { CustomerDocument } from "../../models/customer";
import { JobType } from "../../models/job-type";
import { JobStatus } from "../../models/job-status";
import { BillingType } from "../../models/billing-type";
import { WorkType } from "../../models/work-type";
import { isUndefined, isString, isDate } from "../../services/utils";
import { sortBy } from "../../utilities/sort";
import { guid } from "../../utilities/guid";
import { UserService } from "../../services/data/users-service";
import { Roles, Authentication } from "../../services/auth";
import { contains, equals } from "../../utilities/equals";

interface SelectableOption {
  value: string;
  name: string;
  checked: boolean;
  error?: string;
}

@autoinject()
export class EditJob {
  job: JobDocument;
  customers: CustomerDocument[];
  customerServicePromise: Promise<any>;
  activities: string[];
  jobTypes: JobType[] = JobType.OPTIONS;
  jobStatuses: JobStatus[] = JobStatus.OPTIONS;
  billingTypes: BillingType[] = BillingType.OPTIONS;
  workTypes: WorkType[] = WorkType.OPTIONS;
  isFollowup: boolean = false;
  subjobOptions: SelectableOption[] = [];
  closeOriginalJob: boolean = true;
  subjobChecked: boolean = false;
  salesmen: string[] = [];
  salesmenPromise: Promise<any>;
  projectManagers: string[] = [];
  projectManagersPromise: Promise<any>;
  planningStatuses: string[] = [];
  foremen: string[] = [];
  foremenPromise: Promise<any>;
  foremenReady = false;
  isAdmin = false;
  isConvertedFromQuote = false;

  constructor(
    private element: Element,
    private router: Router,
    private jobService: JobService,
    private customerService: CustomerService,
    private dialogService: DialogService,
    private subJobService: SubJobOptionService,
    private userService: UserService,
    private auth: Authentication
  ) {
    this.customerServicePromise = customerService
      .getAll()
      .then((customers: CustomerDocument[]) => (this.customers = customers))
      .catch(Notifications.error);

    this.salesmenPromise = this.userService
      .getAllInRole(Roles.Foreman)
      .then((salesmen) => (this.salesmen = salesmen.map((f) => f.name)))
      .catch(Notifications.error);

    this.projectManagersPromise = this.userService
      .getAllInRole(Roles.ProjectManager)
      .then((managers) => (this.projectManagers = managers.map((m) => m.name)))
      .catch(Notifications.error);

    this.foremenPromise = this.userService
      .getAllInRole(Roles.Foreman)
      .then((foremen) => (this.foremen = foremen.map((f) => f.name)))
      .catch(Notifications.error);

    this.isAdmin = auth.isInRole(Roles.Administrator);
  }

  async activate(params: any) {
    const id = params.id;
    this.isConvertedFromQuote = params.isConvertedFromQuote;
    const date = moment(params.date, "YYYY-MM-DD");

    // Ensure customers are loaded first (customer dropdown depends on this)
    await this.customerServicePromise;

    if (isUndefined(id)) {
      this.job = new JobDocument();
      if (isString(params.type)) {
        this.job.type = params.type;
      }

      if (!isUndefined(params.date) && date.isValid()) {
        this.job.startDate = date.toDate();
      }

      if (params.from) {
        try {
          const prev: JobDocument = await this.jobService.getOne(params.from);
          this.isFollowup = true;
          this.job.customer = prev.customer;
        } catch (e) {
          Notifications.error(e);
        }
      }
    } else {
      try {
        const job: JobDocument = await this.jobService.getOne(id);
        this.job = job;

        if (job.customer) {
          $(".customer", this.element).dropdown(
            "set selected",
            job.customer.name
          );
          $(".customer", this.element).dropdown("set value", job.customer._id);
        }

        if (job.status) {
          $("#status", this.element).dropdown("set selected", job.status);
          $("#status", this.element).dropdown("set value", job.status);
        }

        if (job.billing_type) {
          $("#billingType", this.element).dropdown(
            "set selected",
            job.billing_type
          );
          $("#billingType", this.element).dropdown(
            "set value",
            job.billing_type
          );
        }

        if (job.planning_status) {
          $("#planningStatus", this.element).dropdown(
            "set selected",
            job.planning_status
          );
          $("#planningStatus", this.element).dropdown(
            "set value",
            job.planning_status
          );
        }

        window.setTimeout(() => {
          if (
            Array.isArray(job.additionalDates) &&
            job.additionalDates.length
          ) {
            job.additionalDates.forEach((d, i) => {
              let date: Date | null = null;
              if (isDate(d[0])) {
                date = d[0];
              } else if (isString(d[0])) {
                date = moment(d[0]).toDate();
              }

              if (date) {
                $(`.calendar.start-${i}`, this.element)
                  .calendar({
                    type: "date",
                    onChange: (d) =>
                      (this.job.additionalDates[i][0] = moment(d).toDate()),
                  })
                  .calendar("set date", date);
              }

              date = null;
              if (isDate(d[1])) {
                date = d[1];
              } else if (isString(d[1])) {
                date = moment(d[1]).toDate();
              }

              if (date) {
                $(`.calendar.end-${i}`, this.element)
                  .calendar({
                    type: "date",
                    onChange: (d) =>
                      (this.job.additionalDates[i][1] = moment(d).toDate()),
                  })
                  .calendar("set date", date);
              }
            });
          }
        }, 100);
      } catch (err) {
        Notifications.error(err);
        this.router.navigateToRoute("jobs.list");
      }
    }

    // Ensure people lists are loaded so dropdown menus are populated before we init in attached
    await Promise.all([
      this.salesmenPromise,
      this.projectManagersPromise,
      this.foremenPromise,
    ]);

    const options = await this.subJobService.getAll();
    this.subjobOptions = options.sort(sortBy("value")).map((o) => ({
      name: o.name,
      value: o.value,
      checked: false,
    }));
  }

  attached() {
    $(".dropdown.customer", this.element).dropdown({
      allowAdditions: true,
      hideAdditions: false,
      fullTextSearch: "exact",
      match: "text",
      onChange: (value: string): void => {
        this.job.customer = this.customers.find((c) => c._id === value);
        if (!this.job.customer) {
          this.job.customer = new CustomerDocument();
          this.job.customer.name = value;
        }
      },
    });
    $(".dropdown.basic.button", this.element).dropdown();
    $("#status", this.element).dropdown();
    $("#billingType", this.element).dropdown();
    $("#workType", this.element).dropdown();
    $(".dropdown.salesman", this.element).dropdown();
    $(".dropdown.project-manager", this.element).dropdown();

    if (this.job && this.job.salesman) {
      $(".dropdown.salesman", this.element).dropdown(
        "set selected",
        this.job.salesman
      );
      $(".dropdown.salesman", this.element).dropdown(
        "set value",
        this.job.salesman
      );
    }
    if (this.job && this.job.project_manager) {
      $(".dropdown.project-manager", this.element).dropdown(
        "set selected",
        this.job.project_manager
      );
      $(".dropdown.project-manager", this.element).dropdown(
        "set value",
        this.job.project_manager
      );
    }
    $("#planningStatus", this.element).dropdown();
    $(".calendar.start", this.element).calendar({
      type: "date",
      onChange: (date) => (this.job.startDate = moment(date).toDate()),
    });
    $(".calendar.end", this.element).calendar({
      type: "date",
      onChange: (date) => (this.job.endDate = moment(date).toDate()),
    });
    $(".calendar.planning-start", this.element).calendar({
      type: "date",
      onChange: (date) => (this.job.planningStartDate = moment(date).toDate()),
    });
    $(".calendar.planning-end", this.element).calendar({
      type: "date",
      onChange: (date) => (this.job.planningEndDate = moment(date).toDate()),
    });

    const isUnassigned =
        !this.job.foreman ||
        (Array.isArray(this.job.foreman) && !this.job.foreman.length),
      values = [
        { name: "Unassigned", value: "Unassigned", selected: isUnassigned },
      ].concat(
        this.foremen.map((name) => {
          const value: any = { name, value: name };
          if (
            (Array.isArray(this.job.foreman) &&
              this.job.foreman.indexOf(name) !== -1) ||
            (typeof this.job.foreman === "string" &&
              equals(this.job.foreman, name))
          ) {
            value.selected = true;
          }

          return value;
        })
      );

    $(".dropdown.foreman", this.element).dropdown({
      onChange: this.onForemanChanged.bind(this),
      // @ts-ignore
      values,
    });

    window.setTimeout(() => (this.foremenReady = true), 50);

    // Set initial calendar values after initialization
    const toDateSafely = (v: any): Date | null => {
      if (isDate(v)) return v as Date;
      if (isString(v)) {
        const m = moment(v as string);
        if (m.isValid()) return m.toDate();
      }
      return null;
    };

    let d0 = toDateSafely(this.job && (this.job as any).startDate);
    if (d0) $(".calendar.start", this.element).calendar("set date", d0);

    let d1 = toDateSafely(this.job && (this.job as any).endDate);
    if (d1) $(".calendar.end", this.element).calendar("set date", d1);

    let d2 = toDateSafely(this.job && (this.job as any).planningStartDate);
    if (d2)
      $(".calendar.planning-start", this.element).calendar("set date", d2);

    let d3 = toDateSafely(this.job && (this.job as any).planningEndDate);
    if (d3) $(".calendar.planning-end", this.element).calendar("set date", d3);

    $(".ui.checkbox", this.element).checkbox();

    const $buttonBar = $(".button-bar", this.element);
    $buttonBar.visibility({
      once: false,
      onBottomPassed: () => {
        $buttonBar.addClass("fixed top");
      },
      onBottomPassedReverse: function () {
        $buttonBar.removeClass("fixed top");
      },
    });
  }

  detached() {
    $(".dropdown.activity", this.element).dropdown("destroy");
    $("#status", this.element).dropdown("destroy");
    $("#billingType", this.element).dropdown("destroy");
    $("#workType", this.element).dropdown("destroy");
    $(".calendar", this.element).calendar("destroy");
    $(".button-bar", this.element).visibility("destroy");
    $(".dropdown.basic.button", this.element).dropdown("destroy");

    $(".ui.checkbox", this.element).checkbox("destroy");
  }

  get customer_id(): string {
    return this.job && this.job.customer ? this.job.customer._id : null;
  }

  set customer_id(value: string) {
    var customer = this.customers.find((c) => c._id === value);
    if (customer) {
      this.job.customer = customer;
    }
  }

  get job_number(): string {
    if (!this.job) {
      return "";
    }

    return (
      (this.job.job_type === JobType.SERVICE_CALL
        ? "S"
        : this.job.job_type === JobType.WORK_ORDER
        ? "W"
        : "P") +
      "-" +
      this.job.number
    );
  }

  get subjobOptionsChecked(): boolean {
    return this.subjobOptions.some((o) => o.checked);
  }

  addDates() {
    if (!Array.isArray(this.job.additionalDates)) {
      this.job.additionalDates = [];
    }

    const length = this.job.additionalDates.length;

    this.job.additionalDates.push([new Date(), new Date()]);

    window.setTimeout(() => {
      $(`.calendar.start-${length}`, this.element)
        .calendar({
          type: "date",
          onChange: (date) =>
            (this.job.additionalDates[length][0] = moment(date).toDate()),
        })
        .calendar("set date", new Date());
      $(`.calendar.end-${length}`, this.element)
        .calendar({
          type: "date",
          onChange: (date) =>
            (this.job.additionalDates[length][1] = moment(date).toDate()),
        })
        .calendar("set date", new Date());
    }, 100);
  }

  removeDates(index: number) {
    const length = $(
      `.calendar.start-${index},.calendar.end-${index}`,
      this.element
    ).length;
    $(`.calendar.start-${index},.calendar.end-${index}`, this.element).calendar(
      "destroy"
    );

    this.job.additionalDates.splice(index, 1);
  }

  addOption() {
    this.subjobOptions.push({ value: "", name: "", checked: true });

    window.setTimeout(() => {
      $(".ui.checkbox", this.element).checkbox("destroy").checkbox();
    }, 100);
  }

  onForemanChanged(value: string) {
    if (this.foremenReady) {
      const existing = Array.isArray(this.job.foreman)
        ? this.job.foreman.join(",")
        : this.job.foreman;

      if (value !== existing) {
        const foreman = contains(value, ",") ? value.split(",") : value;

        this.job.foreman = foreman;
      }
    }
  }

  onJobTypeChanged(value: string) {
    if (value === JobType.WORK_ORDER) {
      //Default salesman to logged in user, if they're a salesman
      if (this.auth.isInRole(Roles.Foreman)) {
        this.job.salesman = this.auth.userInfo().name;
        $(".salesman", this.element).dropdown(
          "set selected",
          this.job.salesman
        );
        $(".salesman", this.element).dropdown("set value", this.job.salesman);
      }

      const customer = this.customers.find((c) =>
        equals(c.name, "Langendoen Projects")
      );

      if (customer) {
        this.job.customer = customer;
        $(".customer", this.element).dropdown(
          "set selected",
          this.job.customer.name
        );
        $(".customer", this.element).dropdown(
          "set value",
          this.job.customer._id
        );
      }

      // Default foreman to unassigned
      this.job.foreman = null;
    }
  }

  onSaveClick() {
    if (this.customer_id) {
      this.saveJob();
    } else {
      this.saveCustomer(this.job.customer)
        .then((customer: CustomerDocument) => {
          this.job.customer = customer;
          this.saveJob();
        })
        .catch(Notifications.error);
    }
  }

  onCreateSubJobsClick() {
    this.dialogService
      .open({
        viewModel: SubJobs,
        model: { job: this.job },
      })
      .whenClosed((result) => {
        if (!result.wasCancelled) {
          this.router.navigateBack();
        }
      });
  }

  onCancelClick() {
    this.router.navigateBack();
  }

  onDeleteClick() {
    this.dialogService
      .open({
        viewModel: Prompt,
        model: "Are you sure you want to delete this job?",
      })
      .whenClosed((result) => {
        if (result.wasCancelled) return;

        this.jobService
          .delete(this.job.toJSON())
          .then(() => {
            Notifications.success("Job Deleted");
            this.router.navigateBack();
          })
          .catch(Notifications.error);
      });
  }

  validateOption(option: SelectableOption) {
    return true;
  }

  saveJob(): Promise<Job | void> {
    if (
      this.closeOriginalJob &&
      this.subjobOptions.filter((o) => o.checked && o.value).length
    ) {
      this.job.status = JobStatus.CLOSED;
    }

    return this.jobService
      .save(this.job.toJSON())
      .then(async (result) => {
        // get the newly created job from id (to get assigned number, _id, etc.)
        const newJob = await this.jobService.getOne(result.id);

        // save sub-jobs (await all saves before navigating)
        const selected = this.subjobOptions.filter((o) => o.checked && o.value);
        if (selected.length) {
          const saves = selected.map((o) => {
            const document = new JobDocument(newJob);
            const json = document.toJSON();

            json._id = guid();
            delete json._rev;
            json.number = `${newJob.number}${(o.value || "")
              .toString()
              .toUpperCase()}`;
            json.status = JobStatus.PENDING;
            json.name = `${newJob.name} - ${o.name}`;

            return this.jobService.save(json).catch((err) => {
              console.error("Failed to save sub-job", json.number, err);
              Notifications.error(err);
              throw err;
            });
          });

          try {
            await Promise.all(saves);
          } catch (e) {
            // already notified above
          }
        }

        Notifications.success("Job Saved");
        this.router.navigateToRoute("jobs.list");
      })
      .catch((err) => {
        Notifications.error(err);
      });
  }

  saveCustomer(customer: CustomerDocument): Promise<CustomerDocument> {
    return this.customerService.create(customer.toJSON());
  }
}
